body {
  background: "black";
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: lavender;
}

div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.leftMenuItemStyle {
  cursor: pointer;
}

.isActive_LeftMenuItemStyle {
  font-weight: bold;
  color: red;
  cursor: pointer;
}

::-webkit-scrollbar {
  border-radius: 6px;
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background: rgba(58, 140, 161, 0.8);
}